import tokens from '../tokens/tokens.json';

function getProperty<T, K extends keyof T>(obj: T, key: K): T[K] {
    return obj[key];
}

// Helper function to parse and apply styles from tokens
// Function to convert token settings to a styled-components compatible CSS string
export const getStylesFromTokens = (
    type: string,
    variant: string,
    size?: string
) => {
    const path = [type, variant, size].filter(Boolean) as Array<
        keyof typeof tokens
    >; // Ensure the path elements are valid keys

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    let settings: any = tokens; // Start with the tokens object
    path.forEach((key) => {
        if (settings && typeof settings === 'object') {
            settings = getProperty(settings, key);
        }
    });

    if (!settings) {
        console.error(
            `Settings not found for type: ${type}, variant: ${variant}, size: ${size}`
        );
        return '';
    }

    return `
        font-family: ${settings.fontFamily}, sans-serif;
        font-weight: ${
            settings.fontWeight === 'Light'
                ? 300
                : settings.fontWeight === 'Regular'
                ? 400
                : settings.fontWeight === 'Semi Bold'
                ? 600
                : settings.fontWeight === 'Bold'
                ? 700
                : 'inherit'
        };
        font-size: ${settings.fontSize}px;
        line-height: ${settings.lineHeight}px;
        text-decoration: ${settings.textDecoration};
    `;
};

// Utility function to determine the default HTML tag
export function getDefaultTag(variant: string): string {
    switch (variant) {
        case 'h1':
        case 'h2':
        case 'h3':
        case 'h4':
        case 'h5':
        case 'h6':
        case 'p':
        case 'ol':
        case 'ul':
            return variant;
        case 'line':
            return 'p';
        case 'list':
            return 'ol';
        default:
            return 'div';
    }
}
