import React from 'react';
import cx from 'classnames';

import { getMarginStyles } from '../_helpers/marginToCss';
import { MarginProps } from '../types';

import { getDefaultTag } from './helpers';
import { HeadingProps, LabelProps, TextProps } from './types';

import styles from './Typography.module.scss';

// Heading component
const Heading: React.FC<MarginProps & HeadingProps> = ({
    id,
    variant,
    as,
    italic = false,
    underline = false,
    weight = '600',
    children,
    className,
    mt,
    mb,
    my,
    ...props
}) => {
    const Component = as || getDefaultTag(variant);
    const baseClassName = styles[`heading-${variant}`];
    const classNames = `
        ${baseClassName}
        ${className}
        ${italic ? styles['text-italic'] : ''}
        ${underline ? styles['text-underline'] : ''}
        ${weight ? styles[`text-${weight}`] : ''}
    `;
    const margins = getMarginStyles({ mt, mb, my });

    return (
        <Component
            id={id}
            className={classNames}
            style={{ ...margins }}
            {...props}
        >
            {children}
        </Component>
    );
};

const Body: React.FC<MarginProps & TextProps> = ({
    variant = 'p',
    size = 'default-400',
    italic = false,
    underline = false,
    weight = '400',
    children,
    className,
    mt,
    mb,
    my,
    ...props
}) => {
    const Component = variant === 'p' ? 'p' : variant === 'list' ? 'ul' : 'div';
    const baseClassName = styles[`text-${variant}-${size}`];
    const classNames = `
        ${baseClassName}
        ${className}
        ${italic ? styles['text-italic'] : ''}
        ${underline ? styles['text-underline'] : ''}
        ${weight ? styles[`text-${weight}`] : ''}
    `;
    const margins = getMarginStyles({ mt, mb, my });
    return (
        <Component className={classNames} style={{ ...margins }} {...props}>
            {children}
        </Component>
    );
};

// Label component with size application
const Label: React.FC<LabelProps> = ({ id, size, children, className }) => {
    const baseClassName = styles[`label-${size}`]; // Use the size to get the correct class name
    const classNames = cx(baseClassName, className); // Use the size to get the correct class name
    return (
        <div id={id} className={classNames}>
            {children}
        </div>
    );
};

export const Text = {
    Heading,
    Body,
    Label,
};
