import React from 'react';
import { styled } from 'styled-components';

import { getMarginStyles } from '../_helpers/marginToCss';
import { MarginProps } from '../types';

import { getDefaultTag, getStylesFromTokens } from './helpers';
import { HeadingProps, LabelProps, TextProps } from './types';

type DynamicTagProps = {
    variant?: string;
} & React.HTMLAttributes<HTMLElement>;

const DynamicTagWrapper = <P extends DynamicTagProps>(
    StyledComponent: React.ComponentType<P>
) => {
    // Return a functional component that accepts DynamicTagProps and forwards them to StyledComponent.
    // Utilize 'as' prop for dynamic element tagging, based on the 'variant' or a default tag.
    const ComponentWithDynamicTag: React.FC<P> = ({ variant, ...props }) => {
        // Determine the tag based on the 'variant' prop or use a default.
        const Tag = getDefaultTag(
            variant ?? 'div'
        ) as keyof JSX.IntrinsicElements;

        // Spread the rest of the props to StyledComponent while dynamically setting the 'as' prop.
        return <StyledComponent as={Tag} variant={variant} {...(props as P)} />;
    };

    return ComponentWithDynamicTag;
};

// Heading component
const StyledHeadingBase = styled.div<HeadingProps & MarginProps>`
    ${({ mt, mb, my }) => getMarginStyles({ mt, mb, my })}
    ${({ variant }) => getStylesFromTokens('heading', 'desktop', variant)};
    @media all and (max-width: 1024px) {
        ${({ variant }) => getStylesFromTokens('heading', 'mobile', variant)};
    }
`;

export const StyledHeading = DynamicTagWrapper(StyledHeadingBase);

// Text component
const StyledTextBase = styled.p<TextProps & MarginProps>`
    ${({ variant = 'p', size = 'default-400' }) =>
        getStylesFromTokens('text', variant, size)}
    ${({ mt, mb, my }) => getMarginStyles({ mt, mb, my })}
`;

export const StyledText = DynamicTagWrapper(StyledTextBase);

// Label component
const StyledLabelBase = styled.label<LabelProps>`
    ${({ size }) => getStylesFromTokens('label', '', size)}
`;

export const StyledLabel = DynamicTagWrapper(StyledLabelBase);

// Export as Typography object
export const Typography = {
    Heading: StyledHeading,
    Text: StyledText,
    Label: StyledLabel,
};
