import React, { forwardRef, ReactNode, useRef } from 'react';
import {
    AriaButtonProps,
    useButton,
    useFocusRing,
    useTooltipTrigger,
} from 'react-aria';
import { useTooltipTriggerState } from 'react-stately';
import { mergeProps, useObjectRef } from '@react-aria/utils';
import classNames from 'classnames';

import Tooltip from './Tooltip';

import styles from './Button.module.scss';

type Variant = 'primary' | 'secondary' | 'tertiary' | 'alert';

export interface ButtonProps extends AriaButtonProps {
    isIcon?: boolean;
    isFullWidth?: boolean;
    className?: string;
    variant?: Variant;
    inverted?: boolean;
    tooltipLabel?: string;
    tooltipWrapperClassName?: string;
    iconLeft?: ReactNode;
    iconRight?: ReactNode;
}

export const Button = forwardRef<HTMLButtonElement, ButtonProps>(
    (props, ref) => {
        const {
            children,
            className,
            variant = 'primary',
            inverted,
            isIcon,
            isFullWidth,
            tooltipLabel,
            tooltipWrapperClassName,
            iconLeft,
            iconRight,
        } = props;
        const forwardedRef = useObjectRef(ref);
        const fallbackRef = useRef(null);
        const buttonRef = forwardedRef || fallbackRef;
        const { buttonProps } = useButton(props, buttonRef);
        const { isFocusVisible, focusProps } = useFocusRing();
        const tooltipTriggerProps = { delay: 0, closeDelay: 0, ...props };
        const state = useTooltipTriggerState(tooltipTriggerProps);
        const { triggerProps, tooltipProps } = useTooltipTrigger(
            tooltipTriggerProps,
            state,
            buttonRef
        );

        const classList = classNames(styles.btn, className, {
            [styles[`btn--${variant}`]]: variant,
            [styles['btn--inverted']]: inverted,
            [styles[`btn--full-width`]]: isFullWidth,
            [styles['btn--icon-only']]: isIcon,
            [styles['btn--has-icons']]: iconLeft || iconRight,
            [styles['focus-ring']]: isFocusVisible,
        });
        const tooltipWrapperClassList = classNames(
            styles.base,
            tooltipWrapperClassName
        );

        const mergedProps = mergeProps(buttonProps, focusProps, triggerProps);

        const RegularButton = (
            <button {...mergedProps} ref={buttonRef} className={classList}>
                {iconLeft}
                <span className={styles.label}>{children}</span>
                {iconRight}
            </button>
        );

        const IconButton = (
            <button {...mergedProps} ref={buttonRef} className={classList}>
                {children}
            </button>
        );

        const IconButtonWithTooltip = (
            <span className={tooltipWrapperClassList}>
                {IconButton}
                <Tooltip state={state} {...tooltipProps}>
                    {tooltipLabel}
                </Tooltip>
            </span>
        );

        switch (true) {
            case isIcon && !tooltipLabel:
                return IconButton;
            case isIcon && tooltipLabel !== undefined:
                return IconButtonWithTooltip;
            default:
                return RegularButton;
        }
    }
);

Button.displayName = 'Button';

export default Button;
